@use "./node.scss";
@use "./nav.scss";
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
*, *::after, *::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    background-color: hsl(0, 0%, 100%);
    font-family: 'Poppins', 'sans-serif';
    overflow: hidden;
}

/* stolen from https://codepen.io/raubaca/pen/bGWmZje */
select {
     /* Reset */
    appearance: none;
    border: 0;
    outline: 0;
    font: inherit;
    /* Personalize */
    width: 20em;
    height: 3em;
    padding: 0 4em 0 1em;
    background: url(https://upload.wikimedia.org/wikipedia/commons/9/9d/Caret_down_font_awesome_whitevariation.svg) no-repeat right 0.8em center / 1.4em, linear-gradient(to left, rgba(255, 255, 255, 0.7) 3em, rgba(255, 255, 255, 0.9) 3em);
    color: rgb(31, 31, 31);
    border-radius: 0.25em;
    box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.select {
    position: relative;
  display: flex;
  width: 7em;
  height: 2em;
  border-radius: .25em;
  overflow: hidden;
}

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;
    padding: 1rem;
}

.head-wrapper {
    display: flex;
    gap: 10px;
    align-items: center;
    margin: 2rem;
}

.range {
    display: flex;
    gap: 15px;
}

#animate {
    border: none;
    border-radius: 10px;
    width: 4rem;
    height: 2rem;
    clear: hsl(0, 0%, 10%);
    background-color: rgb(105, 221, 105);
    transition: all .3s ease; 
}
#clear {
    border: none;
    border-radius: 10px;
    width: 4rem;
    height: 2rem;
    color:hsl(0, 0%, 10%);
    background-color: rgb(221, 105, 105);
    transition: all .3s ease; 
}
#animate:hover {
    transform: scale(1.1);
}
#clear:hover {
    transform: scale(1.1);
}

/* https://www.w3schools.com/howto/howto_js_rangeslider.asp */
#r {
    appearance: none;
  width: 100%; /* Full-width */
  height: 15px; /* Specified height */
  background: #d3d3d3; /* Grey background */
  outline: none; /* Remove outline */
  opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
  -webkit-transition: .2s; /* 0.2 seconds transition on hover */
  transition: opacity .2s;
}

#r::-webkit-slider-thumb {
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    width: 15px; /* Set a specific slider handle width */
    height: 15px; /* Slider handle height */
    background: #04AA6D; /* Green background */
    cursor: pointer; /* Cursor on hover */
  }

  .select-wrapper{
      position: relative;
  }


  #test {
    position: absolute;
    z-index: 2;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    margin: auto 10px;
    cursor: pointer;
    transform: rotate(90deg);
    height: 25px;
    width: 25px;
    pointer-events: none;
  }

.modal-wrapper {
    position: absolute;
    
}

.modal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 440px;
    max-height: 720px;
    min-height: 200px;
    background: gray;
    position: relative;
}
.modal-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;
    width: 100%;
    text-align: center;
    height: 40px;
    background: #3c3c3c;
}


.font-300 {
    font-weight: 600;
}